html,
body {
   font-family: 'Mulish', sans-serif;
   font-size: 10px;
}

a {
   text-decoration: none;
   color: inherit;
}

ul {
   list-style-type: none;
}

.header {
   height: 100vh;
   background: url('./../images/header_bg_img.jpg') no-repeat;
   background-size: cover;
   background-position: center;
}

.container {
   max-width: 1780px;
   padding: 0 30px;
   margin: 0 auto;
}

.header__top {
   position: relative;
   padding: 75px 0;
   margin-bottom: 35px;
}

.header__burger-btn {
   background-color: #0aa566;
   display: none;
   border: none;
   width: 50px;
   position: fixed;
   top: 40px;
   right: 30px;
   border-radius: 8px;
   -webkit-border-radius: 8px;
   -moz-border-radius: 8px;
   -ms-border-radius: 8px;
   -o-border-radius: 8px;
   z-index: 9999;
}

.header__burger-btn-icon {
   max-width: 100%;
}

.header__navigation {
   display: flex;
   justify-content: space-between;
   align-items: center;
   flex-wrap: wrap;
}

.header__menu {
   padding: 10px 0;
}

.menu__list {
   display: flex;
   justify-content: flex-start;
   padding: 0;
}

.menu__item {
   margin-right: 35px;
   font-family: Mulish;
   /* font-size: 1.6rem; */
   font-size: calc(14px + 2 * (100vw / 1980));
   font-weight: 700;
   text-transform: uppercase;
}

.menu__item:last-child {
   margin-right: 0;
}

.menu__link {
   color: #ffffff;
}

.header__logo {
   position: absolute;
   top: 70px;
   left: 50%;
   transform: translateX(-50%);
   -webkit-transform: translateX(-50%);
   -moz-transform: translateX(-50%);
   -ms-transform: translateX(-50%);
   -o-transform: translateX(-50%);
}

.header__right-block {
   max-width: 420px;
   display: flex;
   align-items: center;
   padding: 10px 0;
}

.header__personal {
   position: relative;
}

.personal__list {
   display: flex;
   padding: 0;
   margin-right: 140px;
}

.personal__item {
   margin-right: 45px;
}

.personal__item:last-child {
   margin-right: 0;
}

.count {
   position: relative;
}

.personal__count {
   position: absolute;
   top: -10px;
   left: 25px;
   width: 18px;
   height: 18px;
   background-color: white;
   border-radius: 9px;
   -webkit-border-radius: 9px;
   -moz-border-radius: 9px;
   -ms-border-radius: 9px;
   -o-border-radius: 9px;
   font-size: 10px;
   line-height: 18px;
   text-align: center;
}

.header__personal::after {
   content: "";
   position: absolute;
   top: 15px;
   left: 205px;
   width: 47px;
   height: 2px;
   background: url('./../images/header_line.png') no-repeat;
}

.social__list {
   display: flex;
   padding: 0;
}

.social__item {
   margin-right: 10px;
}

.social__item:last-child {
   margin-right: 0;
}

.btn {
   position: relative;
   height: 60px;
   border-radius: 35px;
   padding: 0 30px;
   box-sizing: border-box;
   font-family: Mulish;
   /* font-size: 21px; */
   font-size: calc(16px + 5 * (100vw / 1980));
   line-height: 1.2;
   color: #ffffff;
   font-weight: 800;
}

.btn--green {
   display: flex;
   align-items: center;
}

.btn--green::after {
   content: "";
   position: absolute;
   right: 40px;
   width: 30px;
   height: 16px;
   background: url('./../images/arrow-yellow.png') no-repeat;
}

.btn--green:hover::after {
   transform: translateX(10px);
   -webkit-transform: translateX(10px);
   -moz-transform: translateX(10px);
   -ms-transform: translateX(10px);
   -o-transform: translateX(10px);
}

.btn--green:active {
   transform: translate(1px, 2px);
   -webkit-transform: translate(1px, 2px);
   -moz-transform: translate(1px, 2px);
   -ms-transform: translate(1px, 2px);
   -o-transform: translate(1px, 2px);
}

.header__btn {
   width: 280px;
   height: 70px;
   background-color: #0aa566;
   /* font-size: 24px; */
   font-size: calc(20px + 4 * (100vw / 1980));
   line-height: 1.2;
   padding: 0 38px;
   margin: 0 auto;
}

.smoothies {
   padding: 90px 30px 125px;
}

.smoothies__wrap {
   display: flex;
   flex-direction: column;
   align-items: center;
}

.title {
   font-family: Mulish;
   font-size: calc(30px + 20 * (100vw / 1980));
   line-height: 1.2;
   font-weight: 800;
   color: #f7ae3b;
   letter-spacing: -0.02em;
}

.smoothies__title {
   margin: 0 0 30px;
   text-align: center;
}

.text {
   font-family: Mulish;
   font-size: calc(16px + 2 * (100vw / 1980));
   line-height: 2.0;
   font-weight: 400;
   color: #1e1e1e;
}

.smoothies__text {
   max-width: 910px;
   text-align: center;
   margin: 0 0 65px;
}

.smoothies__grid {
   display: grid;
   grid-template-columns: 1fr 1fr 1fr 1fr;
   background: #9ca2a2;
   gap: 2px;
}

.grid__item {
   display: flex;
   flex-direction: column;
   justify-content: flex-end;
   align-items: center;
   margin: 0;
   background: white;
   padding: 15px 35px 35px;
}

.grid__img {
   margin-bottom: 20px;
}

.grid__caption {
   font-family: Mulish;
   font-size: calc(15px + 10 * (100vw / 1980));
   line-height: 1.6;
   font-weight: 800;
   color: #f2612d;
   letter-spacing: -0.02em;
}

.about {
   background-color: #16816b;
   padding: 0;
}

.about__wrap {
   display: flex;
   justify-content: center;
}

.about__img {
   width: 100%;
   position: relative;
   top: 65px;
   right: 140px;
}

.about__right-block {
   width: 660px;
   margin: 90px 0 70px;
}

.about__title {
   font-size: calc(35px + 20 * (100vw / 1980));
   line-height: 1.1;
   color: #ffffff;
   margin: 0 0 45px;
}

.about__title--green {
   color: #cae941;
   position: relative;
}

.about__title--green::after {
   content: "";
   position: absolute;
   top: 60px;
   right: -20px;
   width: 450px;
   height: 13px;
   background: url('./../images/about_line.png') no-repeat;
}

.about__list {
   font-family: Mulish;
   font-size: calc(18px + 3 * (100vw / 1980));
   line-height: 2.1;
   font-weight: 400;
   color: #ffffff;
   padding-left: 25px;
   margin-bottom: 40px;
   list-style-image: ('./../images/icons/about.png');
}

.about__item {
   padding-left: 10px;
}

.about__btn {
   width: 380px;
   border: 2px solid #f8b231;
   padding-left: 30px;
}

.products__wrap {
   display: flex;
   overflow: auto;
}

.products__item {
   display: flex;
   flex-direction: column;
   align-items: center;
   flex: 0 1 25%;
   padding: 130px 0 75px;
   min-width: 320px;
}

.products__item--сhocolate {
   background: url('./../images/products_bg_1.png');
}

.products__item--vegan {
   background: url('./../images/products_bg_2.png');
}

.products__item--lemon {
   background: url('./../images/products_bg_3.png');
}

.products__item--turmeric {
   background: url('./../images/products_bg_4.png');
}

.products__img {
   margin-bottom: 30px;
}

.products__title {
   /* font-size: 24px; */
   font-size: calc(22px + 2 * (100vw / 1980));
   line-height: 22px;
   margin: 0 0 5px;
}

.products__caption {
   /* font-size: 21px; */
   font-size: calc(18px + 3 * (100vw / 1980));
   line-height: 32px;
   font-weight: 400;
   margin-bottom: 60px;
}

.products__title,
.products__caption,
.products__btn {
   font-family: Mulish;
   color: #ffffff;
}

.products__btn {
   position: relative;
   background-color: transparent;
   width: 204px;
   border: solid 2px #ffffff;
   border-radius: 30px;
   padding-right: 125px;
}

.products__btn::after {
   content: "";
   position: absolute;
   top: 20px;
   right: 35px;
   width: 30px;
   height: 16px;
   background: url('./../images/arrow-white.png') no-repeat;
}

.products__btn:hover::after {
   transform: translateX(10px);
   -webkit-transform: translateX(10px);
   -moz-transform: translateX(10px);
   -o-transform: translateX(10px);
}

.products__btn:active {
   transform: translate(1px, 2px);
   -webkit-transform: translate(1px, 2px);
   -moz-transform: translate(1px, 2px);
   -ms-transform: translate(1px, 2px);
   -o-transform: translate(1px, 2px);
}

.story {
   padding: 120px 0 160px;
}

.story__wrap {
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
}

.story__content {
   align-self: center;
   margin-right: 200px;
}

.story__title {
   margin: 0 0 30px;
}

.story__text {
   max-width: 515px;
   margin-bottom: 50px;
}

.story__btn {
   width: 300px;
   background-color: #0aa566;
}

.story__img-wrap {
   position: relative;
}

.story__img {
   width: 100%;
}

.story__img-wrap::after {
   content: "";
   position: absolute;
   top: 20px;
   left: -165px;
   width: 290px;
   height: 216px;
   background: url('./../images/story_stamp.png') no-repeat;
}

.testimonials {
   background: url('./../images/testimonials_bg.jpg') no-repeat;
   background-size: cover;
   background-position: center;
   padding: 110px 0 80px;
   z-index: -2;
}

.testimonials__inner {
   position: relative;
}

.testimonials__inner::after {
   content: "";
   position: absolute;
   top: -190px;
   right: 60px;
   width: 410px;
   height: 301px;
   background: url('./../images/testimonials_dots.png') no-repeat;
}

.testimonials__wrap {
   display: flex;
   flex-direction: column;
   align-items: center;
   position: relative;
   z-index: 1;
}

.testimonials__title {
   margin: 0 0 115px;
}

.testimonials__title--red {
   color: #f2612d;
}

.testimonials__personal {
   position: relative;
   padding: 0 120px;
}

.testimonials__content {
   display: flex;
   flex-wrap: wrap;
   margin-bottom: 120px;
}

.testimonials__paragraph {
   display: flex;
   flex-direction: column;
   align-items: center;
   max-width: 460px;
   margin: 55px 50px 0 0;
}

.testimonials__personal::after {
   content: "Sarah A";
   font-family: Mulish;
   font-size: 20rem;
   line-height: 2.0;
   font-weight: 800;
   letter-spacing: -0.02em;
   color: #521905;
   position: absolute;
   top: -165px;
   left: -40px;
   opacity: 0.6;
   z-index: -1;
}

.testimonials__text {
   text-align: center;
   line-height: 3.5rem;
   font-weight: 400;
   color: #ffffff;
   margin-bottom: 20px;
}

.testimonials__caption--orange {
   line-height: 1.2;
   font-weight: 600;
   color: #f7ae3b;
   margin: 0;
}

.testimonials__btn-left,
.testimonials__btn-right {
   position: relative;
   width: 30px;
   height: 16px;
   background: transparent;
   border: none;
}

.testimonials__btn-left {
   margin-right: 95px;
}

.testimonials__btn-left::after {
   content: "";
   position: absolute;
   top: 0px;
   right: 0px;
   width: 30px;
   height: 16px;
   background: url('./../images/testimonials_arrow__left.png') no-repeat;
}

.testimonials__btn-right::after {
   content: "";
   position: absolute;
   top: 0px;
   right: 0px;
   width: 30px;
   height: 16px;
   background: url('./../images/testimonials_arrow__right.png') no-repeat;
}

.testimonials__btn-left:hover::after {
   transform: translateX(-10px);
   -webkit-transform: translateX(-10px);
   -moz-transform: translateX(-10px);
   -ms-transform: translateX(-10px);
   -o-transform: translateX(-10px);
}

.testimonials__btn-right:hover::after {
   transform: translateX(10px);
   -webkit-transform: translateX(10px);
   -moz-transform: translateX(10px);
   -ms-transform: translateX(10px);
   -o-transform: translateX(10px);
}

.blog {
   padding: 90px 0 190px;
}

.blog__wrap {
   display: flex;
   flex-direction: column;
   max-width: 1200px;
   margin: 0 auto;
}

.blog__title {
   margin: 0 0 45px;
}

.blog__content-wrap {
   display: flex;
   justify-content: center;
}

.blog__img-wrap {
   margin-right: 60px;
   position: relative;
}

.blog__img-wrap::after {
   content: "";
   position: absolute;
   top: 70px;
   left: 515px;
   width: 687px;
   height: 12px;
   background: url('./../images/blog_line.png') no-repeat;
   z-index: -1;
}

.blog__img {
   width: 100%;
}

.blog__right-block {
   padding: 125px 45px 0 0;
}

.blog__right-block-header {
   margin-bottom: 35px;
}

.blog__subtitle {
   font-family: Mulish;
   font-size: 3.0rem;
   line-height: 1.3;
   font-weight: 800;
   letter-spacing: -0.02em;
   color: #f2612d;
   margin: 0 0 10px;
}

.blog__time {
   font-size: 16px;
   font-weight: 400;
}

.blog__text {
   max-width: 475px;
   margin: 0 0 0 auto;
}

.blog__link {
   display: inline-block;
   position: relative;
   font-weight: 800;
}

.blog__link::after {
   content: "";
   position: absolute;
   top: 13px;
   right: -30px;
   width: 18px;
   height: 10px;
   background: url('./../images/blog_arrow.png') no-repeat;
}

.blog__link:hover::after {
   transform: translateX(10px);
   -webkit-transform: translateX(10px);
   -moz-transform: translateX(10px);
   -ms-transform: translateX(10px);
   -o-transform: translateX(10px);
}

.instagram {
   padding: 0 0 10px;
}

.instagram__wrap {
   display: flex;
   flex-direction: column;
   overflow: auto;
}

.instagram__title {
   align-self: center;
   display: inline-block;
   position: relative;
   padding-left: 40px;
   margin: 0 0 40px;
   font-family: Mulish;
   font-size: calc(22px + 2 * (100vw / 1980));
   line-height: 1.6;
   font-weight: 800;
   letter-spacing: -0.02em;
   color: #0aa566;
}

.instagram__title::before {
   content: "";
   position: absolute;
   top: 10px;
   left: 2px;
   width: 26px;
   height: 26px;
   background: url('./../images/icon_inst.png') no-repeat;
}

.instagram__list {
   display: flex;
   padding: 0;
   margin: 0;
}

.instagram__item {
   position: relative;
   flex: 0 1 20%;
   object-fit: cover;
   min-width: 320px;
}

.instagram__img {
   width: 100%;
   height: 100%;
}

.instagram__item::before {
   content: "";
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background-image: url('./../images/inst_shadow.png');
   background-size: 100% 100%;
   opacity: 0.3;
}

.instagram__item::after {
   content: "";
   position: absolute;
   bottom: 25px;
   right: 20px;
   width: 26px;
   height: 26px;
   background: url('./../images/icons/inst_icon.png') no-repeat;
}

.footer__header {
   background-color: #f7ac38;
   padding: 0px 0 15px;
}

.footer__header-wrap {
   display: flex;
   padding: 70px 0px 60px;
   flex-wrap: wrap;
}

.footer__logo {
   margin: 65px 15.3vw 0 0;
}

.footer__central-block {
   display: flex;
   margin-right: 6.4vw;
   width: 590px;
}

.footer__subtitle {
   font-family: Mulish;
   font-size: calc(24px + 2 * (100vw / 1980));
   line-height: 1.5;
   font-weight: 800;
   letter-spacing: -0.02em;
   color: #ffffff;
   margin: 0 0 20px;
}

.footer__information {
   margin-right: 7.8vw;
}

.footer__list-info {
   padding-left: 0px;
   white-space: nowrap;
}

.footer__link-info {
   font-family: Mulish;
   font-size: calc(16px + 2 * (100vw / 1980));
   line-height: 1.9;
   font-weight: 700;
   letter-spacing: -0.02em;
   color: #6d450c;
}

.footer__payment {
   width: 280px;
}

.footer__list-payment {
   display: flex;
   flex-wrap: wrap;
   padding-left: 0;
   padding-top: 5px;
}

.footer__payment-item {
   margin-right: 20px;
   margin-bottom: 20px;
}

.footer__form {
   display: flex;
   flex-direction: column;
   position: relative;
}

.footer__subtitle--form {
   margin-bottom: 25px;
}

.footer__form-input {
   width: 495px;
   /* width: 25.8vw; */
   height: 60px;
   border-radius: 30px;
   border: none;
   padding: 20px 0 15px 70px;
   margin-bottom: 20px;
   background-image: url('./../images/ft_input.png');
   background-repeat: no-repeat;
   background-position: 30px;
}

.footer__form-input::placeholder {
   font-family: Mulish;
   font-size: calc(15px + 2 * (100vw / 1980));
   line-height: 2.0;
   font-weight: 800;
   letter-spacing: -0.02em;
   color: #6d450c;
}

.footer__form-btn {
   background-color: transparent;
   border: none;
   font-family: Mulish;
   font-size: calc(14px + 2 * (100vw / 1980));
   line-height: 2.5;
   font-weight: 800;
   text-transform: uppercase;
   letter-spacing: 0.04em;
   color: #6d450c;
   padding-right: 10px;
}

.footer__form::after {
   content: "";
   position: absolute;
   bottom: 5px;
   left: 125px;
   width: 48px;
   height: 43px;
   background: url('./../images/ft_arrow.png') no-repeat;
}

.footer__social {
   display: flex;
   justify-content: flex-end;
   margin: 0;
   transform: translate(-50px, -35px);
}

.footer__social-item {
   margin-right: 25px;
}

.footer__social-item:last-child {
   margin: 0;
}

.footer__bottom {
   display: flex;
   justify-content: center;
   background-color: #6e2b1c;
   padding: 15px 0 15px 0;
   position: relative;
}

.footer__small {
   align-self: center;
   font-family: Mulish;
   font-size: calc(12px + 1 * (100vw / 1980));
   line-height: 2.7;
   font-weight: 400;
   letter-spacing: 0.02em;
   color: #af8a82;
}

.footer__go-top {
   position: fixed;
   bottom: 40px;
   right: 100px;
   width: 50px;
   height: 50px;
   background-color: #f2662f;
   border-radius: 5px;
   -webkit-border-radius: 5px;
   -moz-border-radius: 5px;
   -ms-border-radius: 5px;
   -o-border-radius: 5px;
}

.footer__go-top::before {
   content: "Go top";
   position: absolute;
   top: 35px;
   right: 60px;
   width: 50px;
   font-family: Mulish;
   font-size: calc(14px + 2 * (100vw / 1980));
   line-height: 2.5;
   font-weight: 400;
   letter-spacing: -0.02em;
   color: #f2662f;
}

.footer__go-top::after {
   content: "";
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   width: 13px;
   height: 25px;
   background: url('./../images/go_top_arrow.png') no-repeat;
   -webkit-transform: translate(-50%, -50%);
   -moz-transform: translate(-50%, -50%);
   -ms-transform: translate(-50%, -50%);
   -o-transform: translate(-50%, -50%);
}



@media screen and (max-width: 1795px) {

   .about__img {
      right: 35px;
   }

   .about__title--green::after {
      width: 100%;
      right: 0px;
   }

   .footer__header-wrap {
      justify-content: space-around;
   }

   .footer__logo {
      margin: 65px 5px 0 0;
   }

   .footer__information {
      margin-right: 100px;
   }

   .footer__central-block {
      justify-content: flex-end;
      width: 640px;
      margin-right: 70px;
   }
}

@media screen and (max-width: 1499px) {

   .header__top {
      padding: 75px 0 30px;
   }

   .header__navigation {
      flex-direction: column;
      justify-content: flex-end;
      padding-top: 110px;
   }

   .about__img {
      right: 20px;
   }

   .testimonials__inner::after {
      display: none;
   }

   .story__content {
      padding-top: 0px;
      margin: 0 150px 70px 0;
   }

   .blog {
      padding: 80px 0 140px;
   }

   .footer__header-wrap {
      padding: 30px 0 0;
   }

   .footer__logo {
      margin: 65px 5px 0 0;
   }

   .footer__central-block {
      margin: 0 0 0 70px;
   }

   .footer__information {
      margin-right: 15vw;
   }

   .footer__subtitle {
      padding-top: 20px;
   }
}

@media screen and (max-width: 1274px) {

   .about__btn .blog__container {
      max-width: 740px;
      padding: 100px 30px 100px;
   }

   .story {
      padding: 10px 70px 75px;
   }

   .story__content {
      padding-top: 50px;
   }

   .story__img-wrap {
      margin: 0 auto;
   }

   .blog__img-wrap::after {
      display: none;
   }

   .blog__wrap-img {
      max-width: 738px;
      margin: 0;
   }

   .blog__right-block {
      padding: 25px 0;
   }

}

@media screen and (max-width: 1120px) {

   .testimonials {
      padding: 115px 0 75px;
   }

   .testimonials__title {
      margin: 0 0 100px;
