  background-size: cover;
  background-position: center; }

.container {
  max-width: 1780px;
  padding: 0 30px;
  margin: 0 auto; }

.header__top {
  position: relative;
  padding: 75px 0;
  margin-bottom: 35px; }

.header__burger-btn {
  background-color: #0aa566;
  display: none;
  border: none;
  width: 50px;
  position: fixed;
  top: 40px;
  right: 30px;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  z-index: 9999; }

.header__burger-btn-icon {
  max-width: 100%; }

.header__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

.header__menu {
  padding: 10px 0; }

.menu__list {
  display: flex;
  justify-content: flex-start;
  padding: 0; }

.menu__item {
  margin-right: 35px;
  font-family: Mulish;
  /* font-size: 1.6rem; */
  font-size: calc(14px + 2 * (100vw / 1980));
  font-weight: 700;
  text-transform: uppercase; }

.menu__item:last-child {
  margin-right: 0; }

.menu__link {
  color: #ffffff; }

.header__logo {
  position: absolute;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%); }

.header__right-block {
  max-width: 420px;
  display: flex;
  align-items: center;
  padding: 10px 0; }

.header__personal {
  position: relative; }

.personal__list {
  display: flex;
  padding: 0;
  margin-right: 140px; }

.personal__item {
  margin-right: 45px; }

.personal__item:last-child {
  margin-right: 0; }

.count {
  position: relative; }

.personal__count {
  position: absolute;
  top: -10px;
  left: 25px;
  width: 18px;
  height: 18px;
  background-color: white;
  border-radius: 9px;
  -webkit-border-radius: 9px;
  -moz-border-radius: 9px;
  -ms-border-radius: 9px;
  -o-border-radius: 9px;
  font-size: 10px;
  line-height: 18px;
  text-align: center; }

.header__personal::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 205px;
  width: 47px;
  height: 2px;
  background: url("./../images/header_line.png") no-repeat; }

.social__list {
  display: flex;
  padding: 0; }

.social__item {
  margin-right: 10px; }

.social__item:last-child {
  margin-right: 0; }

.btn {
  position: relative;
  height: 60px;
  border-radius: 35px;
  padding: 0 30px;
  box-sizing: border-box;
  font-family: Mulish;
  /* font-size: 21px; */
  font-size: calc(16px + 5 * (100vw / 1980));
  line-height: 1.2;
  color: #ffffff;
  font-weight: 800; }

.btn--green {
  display: flex;
  align-items: center; }

.btn--green::after {
  content: "";
  position: absolute;
  right: 40px;
  width: 30px;
  height: 16px;
  background: url("./../images/arrow-yellow.png") no-repeat; }

.btn--green:hover::after {
  transform: translateX(10px);
  -webkit-transform: translateX(10px);
  -moz-transform: translateX(10px);
  -ms-transform: translateX(10px);
  -o-transform: translateX(10px); }

.btn--green:active {
  transform: translate(1px, 2px);
  -webkit-transform: translate(1px, 2px);
  -moz-transform: translate(1px, 2px);
  -ms-transform: translate(1px, 2px);
  -o-transform: translate(1px, 2px); }

.header__btn {
  width: 280px;
  height: 70px;
  background-color: #0aa566;
  /* font-size: 24px; */
  font-size: calc(20px + 4 * (100vw / 1980));
  line-height: 1.2;
  padding: 0 38px;
  margin: 0 auto; }

.smoothies {
